import React, { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";
import CommonBtns from "../../../../components/common-buttons";
import PageHeader from "../../../../components/page-header";
import Axios from "../../../../config/axios-config";
import { AgGridReact } from "ag-grid-react";
import { CustomDate } from "../../../../utilities/custom-date";
import EditIcon from "@mui/icons-material/Edit";
import ClearIcon from "@mui/icons-material/Clear";
import { CustomActive } from "../../../../utilities/custom-active";

const AreaList = () => {
  const admin = useSelector((state) => state.auth.admin);
  const [areaList, setAreaList] = useState([]);
  const gridRef= useRef();

  useEffect(() => {
    Axios({
      url: "getAllAreaDto/" + admin.id,
      method: "get",
    }).then((res) => {
      setAreaList((areaList) => [...areaList, ...res.data]);
    });
  }, []);

  const defaultColDef = {
    sortable: true,
    unSortIcon: true,
    wrapText: true,
    resizable: true,
    autoHeight: true,
  };

  const columnDefs = [
    { headerName: "ID", field: "serialNo" },
    {
      headerName: "Status",
      field: "status",
      cellRenderer: (data) => {
        return CustomActive(data.data.status);
      },
    },
    { headerName: "Name", field: "aName" },
    { headerName: "No of Customers", field: "customerCount" },
    { headerName: "No of Supervisors", field: "supervisorCount" },
    {
      headerName: "Last Updated on",
      field: "aDate",
      valueFormatter: (data) => {
        return CustomDate(data.data.aDate);
      },
    },
    {
      headerName: "Actions",
      width: 125,
      cellRenderer: (data) => {
        // here, I was hoping the "data" would refer to the entire
        // row / object being bound, so that I could check for
        // certain conditions to be true (or false)
        return (
          <>
            <EditIcon sx={{ margin: "5px" }} />
            <ClearIcon sx={{ margin: "5px" }} />{" "}
          </>
        );
      },
    },
  ];

  const handleButtonClick = () => {
    console.log("handleButtonClick");
  };

  return (
    <>
      <PageHeader header={"Area Management"} />
      <CommonBtns
        btnText={"Add Area"}
        handleButtonClick={handleButtonClick}
      ></CommonBtns>
      <$GridHolder className="ag-theme-alpine">
        <AgGridReact
        ref={gridRef}
          rowData={areaList}
          columnDefs={columnDefs}
          defaultColDef={defaultColDef}
          pagination={true}
          paginationPageSize={10}
          enableRangeSelection={true}
        ></AgGridReact>
      </$GridHolder>
    </>
  );
};

export default AreaList;

const $GridHolder = styled.div`
  height: 33em;
  max-width: 100em;
  margin: 1em;
`;
