import { Button } from "@mui/material";
import React from "react";
import { styled, alpha } from "@mui/material/styles";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import S from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFileExcel, faFilePdf } from "@fortawesome/free-solid-svg-icons";

const StyledMenu = styled((props) => (
  <Menu
    elevation={0}
    anchorOrigin={{
      vertical: "bottom",
      horizontal: "right",
    }}
    transformOrigin={{
      vertical: "top",
      horizontal: "right",
    }}
    {...props}
  />
))(({ theme }) => ({
  "& .MuiPaper-root": {
    borderRadius: 6,
    marginTop: theme.spacing(1),
    minWidth: 180,
    color:
      theme.palette.mode === "light"
        ? "rgb(55, 65, 81)"
        : theme.palette.grey[300],
    boxShadow:
      "rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px",
    "& .MuiMenu-list": {
      padding: "10px 0",
      display: "flow-root",
    },
    "& .MuiMenuItem-root": {
      display: "block",
      margin: "0.5em",
      "& .MuiSvgIcon-root": {
        fontSize: 18,
        color: theme.palette.text.secondary,
        margin: theme.spacing(1.5),
      },
      "&:active": {
        backgroundColor: alpha(
          theme.palette.primary.main,
          theme.palette.action.selectedOpacity
        ),
      },
    },
  },
}));

const CommonBtns = ({ btnText, handleButtonClick }) => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <$Div>
      {/* <$RightGroupBtn variant="contained" size="small" bgcolor="red">
        PDF
      </$RightGroupBtn>
      <$RightGroupBtn variant="contained" size="small" bgcolor="#1d6f42">
        Excel
      </$RightGroupBtn> */}
      <div>
        <$RightGroupBtn
          id="export-button"
          aria-controls={open ? "demo-customized-menu" : undefined}
          aria-haspopup="true"
          aria-expanded={open ? "true" : undefined}
          variant="contained"
          disableElevation
          onClick={handleClick}
          endIcon={<KeyboardArrowDownIcon />}
          style={{ height: "32px" }}
        >
          Export
        </$RightGroupBtn>
        <StyledMenu
          id="demo-customized-menu"
          MenuListProps={{
            "aria-labelledby": "demo-customized-button",
          }}
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
        >
          <MenuItem onClick={handleClose} disableRipple>
            <FontAwesomeIcon color="#008000" icon={faFileExcel} />
            <$ExportSpan>Excel</$ExportSpan>
          </MenuItem>
          <MenuItem onClick={handleClose} disableRipple>
            <FontAwesomeIcon color="red" icon={faFilePdf} />
            <$ExportSpan>PDF</$ExportSpan>
          </MenuItem>
        </StyledMenu>
      </div>
      <$RightGroupBtn
        variant="contained"
        size="small"
        color="primary"
        onClick={handleButtonClick}
      >
        {btnText}
      </$RightGroupBtn>
    </$Div>
  );
};

export default CommonBtns;

const $Div = S.div`
  margin: 0.3em 0.3em 0.3em 1em;
  position: relative;
    height: 2em;
`;

const $RightGroupBtn = S(Button)`
  &&& {
    position: relative;
    float: right;
    margin: 0 0.3em;
    background-color: ${(prop) => prop.bgcolor};
    color: white;
  }
`;

const $ExportSpan = S.span`
  margin: 0.5em;
`;
