import React from "react";
import { Navigate, Route, Routes, useLocation } from "react-router-dom";
import Login from "../pages/login/login";
import Main from "../pages/main/main";
import AdminDashboard from "../pages/main/master/admin-dashboard";
import AreaList from "../pages/main/master/area/area-list";
import CustomerList from "../pages/main/master/customer/customer-list";
import MyProfile from "../pages/profile/my-profile";

const CustomRoutes = () => {

  return (
    <Routes>
      <Route
        path="/"
        element={
          <AuthenticatedRoute>
            <Main />
          </AuthenticatedRoute>
        }
      >
        <Route path="dashboard" element={<AdminDashboard />}></Route>
        <Route path="my-profile" element={<MyProfile />}></Route>
        <Route path="area" element={<AreaList />}></Route>
        <Route path="customer" element={<CustomerList />}></Route>
        {/* <Route path="*" element={<Navigate to="dashboard" replace />} /> */}
      </Route>
      <Route path="/login" element={<Login />} />
    </Routes>
  );
};

export default CustomRoutes;

const AuthenticatedRoute = ({ children }) => {
  const location = useLocation();

  const basicAuth = sessionStorage.getItem("basicauth");
  if (!basicAuth) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }
  return children;
};
