import moment from "moment";

export const CustomDate = (date) => {
    let customDate;
    if(date != null) {
        if (date.length > 3) {
            customDate = date[2] + '-' + date[1] + '-' + date[0] + ' ' + date[3] + ':' + date[4] + ':' + date[5];
        } else {
            customDate = moment(String(date[2] + '-' + date[1] + '-' + date[0]), 'D-M-YYYY').format('DD-MM-YYYY');
        }
    }
    return customDate;
}