import React, { useEffect, useState } from "react";
import Accordion from "@mui/material/Accordion";
import {
  AccordionDetails,
  AccordionSummary,
  List,
  ListItem,
} from "@mui/material";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import ListItemText from "@mui/material/ListItemText";
import { MasterMenu, ReportsMenu } from "../utilities/side-menu";
import { useNavigate } from "react-router-dom";
import MuiDrawer from "@mui/material/Drawer";
import styled from "styled-components";

const SideDrawer = ({ openDrawer, closeDrawer }) => {
  const [isOpen, setIsOpen] = useState(openDrawer);
  const [width, setWidth] = useState("0px");
  const navigate = useNavigate();

  useEffect(() => {
    setIsOpen(openDrawer);
    if (openDrawer) {
      setWidth("225px");
    } else {
      setWidth("0px");
    }
  }, [openDrawer]);

  const toggleDrawer = (value) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setIsOpen(value);
    closeDrawer();
  };

  const handleClick = (menu) => {
    // closeDrawer();
    navigate(menu.link, { replace: true });
  };

  return (
    <$Drawer
      variant="permanent"
      anchor={"left"}
      open={isOpen}
      onClose={toggleDrawer(false)}
      width={width}
    >
      <Accordion sx={{ margin: "0px !important" }}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography>Master</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <List>
            {MasterMenu.map((menu) => (
              <ListItem
                button
                key={menu.name}
                onClick={() => handleClick(menu)}
              >
                <ListItemText primary={menu.name} sx={{ color: "#337ab7" }} />
              </ListItem>
            ))}
          </List>
        </AccordionDetails>
      </Accordion>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography>Reports</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <List>
            {ReportsMenu.map((menu) => (
              <ListItem button key={menu.name}>
                <ListItemText primary={menu.name} sx={{ color: "#337ab7" }} />
              </ListItem>
            ))}
          </List>
        </AccordionDetails>
      </Accordion>
    </$Drawer>
  );
};

export default SideDrawer;

