//   shouldForwardProp: (prop) => prop !== "color" && prop !== 'myProp',
// })(({open}) => ({
//   "& .MuiDrawer-paper": {
//     top: '4em',
//     height: '90%',
//     marginBottom: '8px',
//     width: open === 'true' ? '225px' : '0px',
//   }
// }));

const $Drawer = styled(MuiDrawer)`
  transition: width 2s;
  -webkit-transition: width 2s;
  .MuiDrawer-paper {
    top: 4em;
    height: 90%;
    margin-bottom: 8px;
    width: ${(prop) => prop.width};
    transition: width 0.5s;
    -webkit-transition: width 0.5s;
  }
`;
