import { FormControl, InputLabel, MenuItem, Select } from "@mui/material";
import React from "react";
import styled from "styled-components";

const CustomColumnFilterDropdown = ({ options = [], currentValue = '' }) => {
  return (
    <$Div>
      <FormControl
        fullWidth
        sx={{
          "& .MuiOutlinedInput-notchedOutline": {
            height: "28px",
            top: "5px",
          },
          "& .MuiSelect-root": {
            height: "28px",
            top: "-8px",
          },
        }}
      >
        <InputLabel id="custom-filter-dropdown"></InputLabel>
        <Select
          labelId="custom-filter-dropdown-label"
          id="custom-filter-dropdown-select"
          value={currentValue}
          // onChange={handleChange}
        >
          {options.map((o, index) => (
            <MenuItem key={index} value={o}>
              {o}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </$Div>
  );
};

export default CustomColumnFilterDropdown;

const $Div = styled.div`
  position: relative;
`;
