import React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import MenuItem from "@mui/material/MenuItem";
import Menu from "@mui/material/Menu";
import AccountCircle from "@mui/icons-material/AccountCircle";
import SideDrawer from "../../components/custom-drawer";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { logout } from "../../store/auth-slice";
import { useNavigate } from "react-router";
import { Navigate, Outlet, Route, Routes } from "react-router-dom";
import styled from "styled-components";
import { setOpenDrawer } from "../../store/main-menu-slice";

const Main = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  // const [auth, setAuth] = useState(true);
  const [anchorEl, setAnchorEl] = useState(null);
  const [isOpen, setIsOpen] = useState(false);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleMyProfile = () => {
    handleClose();
    navigate("/my-profile", { replace: true });
  };

  const handleLogout = () => {
    handleClose;
    sessionStorage.removeItem("basicauth");
    sessionStorage.removeItem("username");
    dispatch(logout());
    navigate("/login", { replace: true });
  };

  return (
    <>
      <Box sx={{ flexGrow: 1, position: "sticky", zIndex: 10000 }}>
        <AppBar position="static">
          <Toolbar>
            <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 2 }}
              onClick={() => {
                setIsOpen(!isOpen);
                dispatch(setOpenDrawer(!isOpen))
              }}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
              Cable Vasool
            </Typography>
            <div>
              <IconButton
                size="large"
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleMenu}
                color="inherit"
              >
                <AccountCircle />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "right",
                }}
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                open={Boolean(anchorEl)}
                onClose={handleClose}
              >
                <MenuItem onClick={handleMyProfile}>My Profile</MenuItem>
                <MenuItem onClick={handleLogout}>Logout</MenuItem>
              </Menu>
            </div>
          </Toolbar>
        </AppBar>
      </Box>
      {/* {isOpen && (
        <SideDrawer openDrawer={isOpen} closeDrawer={() => setIsOpen(false)} />
      )} */}
      <SideDrawer openDrawer={isOpen} closeDrawer={() => setIsOpen(false)} />

      {
        <Routes>
          {/* <Route path="dashboard" element={<AdminDashboard />} /> */}
          <Route path="/" element={<Navigate to="dashboard" replace />} />
        </Routes>
      }
      <$MainDiv isOpen={isOpen}>
        <Outlet />
      </$MainDiv>
    </>
  );
};

export default Main;

const $MainDiv = styled.div`
  border: 1px solid #e7dede;
  margin: 10px;
  border-radius: 5px;
  box-shadow: 5px 5px 5px 5px #e7dede;
  max-height: 100%;
  overflow: scroll;
  position: fixed;
  left: ${prop => prop.isOpen ? `240px` : `0%`};
  width: ${prop => prop.isOpen ? `80%` : `98%`};
  transition: left 0.5s;
  -webkit-transition: left 0.5s;
`;
