import {
  Button,
  FormControl,
  FormControlLabel,
  Grid,
  InputAdornment,
  Radio,
  RadioGroup,
  TextField,
  Typography,
} from "@mui/material";
import { Formik } from "formik";
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import PageHeader from "../../components/page-header";
import Axios from "../../config/axios-config";
import * as Yup from "yup";

const MyProfile = () => {
  const navigate = useNavigate();
  const [admin, setAdmin] = useState({});

  useEffect(() => {
    Axios({
      url: "findAdmin/" + sessionStorage.getItem("username"),
      method: "get",
    }).then((res) => {
      setAdmin((admin) => ({ ...admin, ...res.data }));
    });
    // console.log("admin", admin);
  }, [admin.username]);

  const handleCancel = () => {
    navigate("/dashboard", { replace: true });
  };

  const saveAdmin = (adminValue) => {
    Axios({
      url: "updateAdmin",
      method: "post",
      data: adminValue,
    }).then((res) => {
      navigate("/dashboard", { replace: true });
    });
  };

  const adminSchema = Yup.object().shape({
    username: Yup.string().required("Required"),
    password: Yup.string().required("Required"),
    name: Yup.string().required("Required"),
    companyName: Yup.string().required("Required"),
    mobile: Yup.string().min(10).max(10).required("Required"),
  });

  return (
    <>
      <PageHeader header={"Edit Profile"} />
      <$Div>
        <Formik
          enableReinitialize
          initialValues={{
            username: admin.username,
            password: admin.password,
            name: admin.name,
            companyName: admin.companyName,
            mobile: admin.mobile,
            location: admin.location,
            headerInfo: admin.headerInfo,
            headerInfo1: admin.headerInfo1,
            smsTextEnglish: admin.smsTextEnglish,
            smsTextTamil: admin.smsTextTamil,
            smsMode: admin.smsMode,
            language: admin.language,
            id: admin.id,
            adminSmsServer: admin.adminSmsServer,
            address: admin.address,
            lastLogin: admin.lastLogin,
            payment: admin.payment,
            profileChange: admin.profileChange,
            remarks: admin.remarks,
            renewal: admin.renewal,
            smsCount: admin.smsCount,
            smsSever: admin.smsSever,
            status: admin.status,
            upiId: admin.upiId,
          }}
          validationSchema={adminSchema}
          onSubmit={(values) => {
            // console.log(JSON.stringify(values, null, 2));
            saveAdmin(values);
          }}
        >
          {({ values, setFieldValue, handleSubmit, handleChange, errors }) => (
            <form onSubmit={handleSubmit}>
              {console.log("error", errors)}
              <Grid item xs={4}>
                <$Typography sx={{ color: "red" }}>
                  {errors.mobile && "Enter valid Mobile Number"}
                  {errors.password && "Enter valid password"}
                  {errors.name && "Enter valid Name"}
                  {errors.companyName && "Enter valid Company Name"}
                </$Typography>
              </Grid>
              {errors && <Grid containersx={{ margin: "0.5em 0" }}></Grid>}
              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>
                    Username<$RequireSpan>*</$RequireSpan>
                  </$Typography>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    fullWidth
                    id="username"
                    name="username"
                    value={values.username}
                    onChange={handleChange}
                    disabled
                  />
                </Grid>
              </Grid>
              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>
                    Password<$RequireSpan>*</$RequireSpan>
                  </$Typography>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    fullWidth
                    id="password"
                    name="password"
                    value={values.password}
                    onChange={handleChange}
                  />
                </Grid>
              </Grid>

              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>
                    Name<$RequireSpan>*</$RequireSpan>
                  </$Typography>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    fullWidth
                    id="name"
                    name="name"
                    value={values.name}
                    onChange={handleChange}
                  />
                </Grid>
              </Grid>

              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>
                    Company Name<$RequireSpan>*</$RequireSpan>
                  </$Typography>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    fullWidth
                    id="companyName"
                    name="companyName"
                    value={values.companyName}
                    onChange={handleChange}
                  />
                </Grid>
              </Grid>

              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>
                    Mobile<$RequireSpan>*</$RequireSpan>
                  </$Typography>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    fullWidth
                    id="mobile"
                    name="mobile"
                    value={values.mobile}
                    onChange={handleChange}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">+91</InputAdornment>
                      ),
                    }}
                  />
                </Grid>
              </Grid>

              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>Location</$Typography>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    fullWidth
                    id="location"
                    name="location"
                    value={values.location}
                    onChange={handleChange}
                  />
                </Grid>
              </Grid>
              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>Bill Header One</$Typography>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    fullWidth
                    id="headerInfo"
                    name="headerInfo"
                    value={values.headerInfo}
                    onChange={handleChange}
                  />
                </Grid>
              </Grid>

              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>Bill Header Two</$Typography>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    fullWidth
                    id="headerInfo1"
                    name="headerInfo1"
                    value={values.headerInfo1}
                    onChange={handleChange}
                  />
                </Grid>
              </Grid>

              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>English SMS Content</$Typography>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    fullWidth
                    id="smsTextEnglish"
                    name="smsTextEnglish"
                    value={values.smsTextEnglish}
                    onChange={handleChange}
                    multiline
                    disabled
                  />
                </Grid>
              </Grid>

              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>Tamil SMS Content</$Typography>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    fullWidth
                    id="smsTextTamil"
                    name="smsTextTamil"
                    value={values.smsTextTamil}
                    onChange={handleChange}
                    multiline
                    disabled
                  />
                </Grid>
              </Grid>

              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>SMS Language</$Typography>
                </Grid>
                <Grid item xs={6}>
                  <FormControl>
                    <RadioGroup
                      row
                      aria-labelledby="sms-mode-group-label"
                      name="smsMode"
                      id="smsMode"
                      value={values.smsMode ?? false}
                      onChange={(event) => {
                        setFieldValue("smsMode", event.currentTarget.value);
                      }}
                    >
                      <FormControlLabel
                        value={true}
                        control={<Radio />}
                        label="English"
                        // onChange={() => formik.values.smsMode = true}
                      />
                      <FormControlLabel
                        value={false}
                        control={<Radio />}
                        label="Tamil"
                        // onChange={() => formik.values.smsMode = false}
                      />
                    </RadioGroup>
                  </FormControl>
                </Grid>
              </Grid>

              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={3}>
                  <$Typography>App Language</$Typography>
                </Grid>
                <Grid item xs={6}>
                  <FormControl>
                    <RadioGroup
                      row
                      aria-labelledby="app-language-radio-buttons-group-label"
                      name="language"
                      id="language"
                      value={values.language ?? 1}
                      onChange={(event) => {
                        setFieldValue("language", event.currentTarget.value);
                      }}
                    >
                      <FormControlLabel
                        value={1}
                        control={<Radio />}
                        label="English"
                      />
                      <FormControlLabel
                        value={2}
                        control={<Radio />}
                        label="Regional"
                      />
                      <FormControlLabel
                        value={3}
                        control={<Radio />}
                        label="Both"
                      />
                    </RadioGroup>
                  </FormControl>
                </Grid>
              </Grid>

              <Grid container spacing={2} sx={{ margin: "0.5em 0" }}>
                <Grid item xs={4}>
                  <Button
                    color="error"
                    variant="contained"
                    fullWidth
                    type="submit"
                    onClick={handleCancel}
                  >
                    Cancel
                  </Button>
                </Grid>
                <Grid item xs={4}>
                  <Button
                    color="primary"
                    variant="contained"
                    fullWidth
                    type="submit"
                    disabled={Object.keys(errors).length !== 0}
                  >
                    Submit
                  </Button>
                </Grid>
              </Grid>
            </form>
          )}
        </Formik>
      </$Div>
    </>
  );
};

export default MyProfile;

const $Div = styled.div`
  margin: 0 10em;
`;

const $Typography = styled(Typography)`
  padding-top: 0.4em;
  font-size: 1.2rem;
  float: right;
  padding-right: 1em;
`;

const $RequireSpan = styled.span`
  color: red;
`;
