import { createSlice } from "@reduxjs/toolkit";
import { customerColumn } from "../utilities/const/customer";

export const customerColumnSlice = createSlice({
  name: "customerColumnSlice",
  initialState: {
    customerSelectedColumn: customerColumn,
  },
  reducers: {
    setCustomerSelectedColumn: (state, action) => {
      state.customerSelectedColumn = action.payload;
    },
  },
});

export const { setCustomerSelectedColumn } = customerColumnSlice.actions;

export default customerColumnSlice.reducer;
