import React from "react";
import Card from "@mui/material/Card";
import styled from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowAltCircleRight } from "@fortawesome/free-solid-svg-icons";

export const CustomCard = ({ card, count = 0 }) => {
  return (
    <$Card bgcolor={card.backgroundColor}>
      <$CardIcon>
        <FontAwesomeIcon icon={card.icon} />
      </$CardIcon>
      <$CardDetails>
        <$CardCount textColor={card.textColor}>
          <span>{count}</span>
        </$CardCount>
        <$CardDesc textColor={card.textColor}>{card.desc}</$CardDesc>
      </$CardDetails>
      <$ViewMore bgcolor={card.linkColor}>
        <span style={{ color: card.textColor }}>View More</span>
        <$FontAwesomeIcon icon={faArrowAltCircleRight} />
      </$ViewMore>
    </$Card>
  );
};

const $CardDetails = styled.div`
  position: relative;
  right: 15px;
  padding-right: 15px;
`;

const $CardCount = styled.div`
  padding-top: 25px;
  text-align: right;
  font-size: 34px;
  line-height: 36px;
  letter-spacing: -1px;
  margin-bottom: 0;
  font-weight: 300;
  color: ${(props) => props.textColor};
`;

const $CardIcon = styled.div`
  width: 80px;
  height: 80px;
  display: block;
  float: left;
  padding-top: 10px;
  padding-left: 15px;
  margin-bottom: 15px;
  font-size: 35px;
  line-height: 35px;
  svg {
    display: inline-block;
    font: 14px/1 FontAwesome;
    margin-left: -35px;
    font-size: 110px;
    line-height: 110px;
    color: #fff;
    opacity: 0.1;
  }
`;

const $CardDesc = styled.div`
  text-align: right;
  font-size: 16px;
  letter-spacing: 0;
  font-weight: 300;
  color: ${(props) => props.textColor};
  opacity: 1;
`;

const $ViewMore = styled.a`
  bottom: -2em;
  clear: both;
  display: block;
  padding: 6px 10px;
  position: relative;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 11px;
  opacity: 0.7;
  color: #fff;
  background-color: ${(props) => props.bgcolor};
  text-decoration: none;
`;

const $FontAwesomeIcon = styled(FontAwesomeIcon)`
  display: inline-block;
  margin-top: 1px;
  float: right;
  background-position: -27px -10px;
`;
const $Card = styled(Card)`
  display: block;
  margin: 1em;
  overflow: hidden;
  border-radius: 4px;
  background-color: ${(props) => props.bgcolor};
  width: 20em;
  height: 10em;
`;
