import React from "react";
import styled from "styled-components";

export const CustomActive = (active) => {
  if (active) {
    return (
      <div>
        <$ActiveSpan>Active</$ActiveSpan>
      </div>
    );
  } else {
    return (
      <div>
        <$InActiveSpan>InActive</$InActiveSpan>
      </div>
    );
  }
};

const $ActiveSpan = styled.span`
  color: #067dc1;
  border: 1px solid;
  padding: 1px;
`;

const $InActiveSpan = styled.span`
  color: red;
  border: 1px solid;
  padding: 1px;
`;
