import { combineReducers } from 'redux';

import authReducer from '../store/auth-slice';
import mainMenuSlice from '../store/main-menu-slice';
import customerColumnSlice from '../store/customer-colimn-slice';

const rootReducer = combineReducers({
	auth: authReducer,
	mainMenu: mainMenuSlice,
	customerSelectedColumn: customerColumnSlice,
});

export default rootReducer;