import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Axios from "../../../../config/axios-config";
import { CustomActive } from "../../../../utilities/custom-active";
import EditIcon from "@mui/icons-material/Edit";
import ClearIcon from "@mui/icons-material/Clear";
import PageHeader from "../../../../components/page-header";
import CommonBtns from "../../../../components/common-buttons";
import { AgGridReact } from "ag-grid-react";
import styled from "styled-components";
import moment from "moment";
import CustomColumnFilterDropdown from "../../../../utilities/custom-column-filter-dropdown";
import { CustomFilterInput } from "../../../../utilities/custom-filter-input";
import CustomFilter from "../../../../components/custom-filter";
import Button from "@mui/material/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUpload } from "@fortawesome/free-solid-svg-icons";
import { CustomPagination } from "../../../../components/pagination";
import { customerColumn } from "../../../../utilities/const/customer";
import { MultiSelect } from "react-multi-select-component";
import { setCustomerSelectedColumn } from "../../../../store/customer-colimn-slice";
import "ag-grid-enterprise";

const CustomerList = () => {
  const dispatch = useDispatch();
  const admin = useSelector((state) => state.auth.admin);
  const drawer = useSelector((state) => state.mainMenu.openDrawer);
  const customerSelectedColumn = useSelector(
    (state) => state.customerSelectedColumn.customerSelectedColumn
  );
  const [customerList, setCustomerList] = useState([]);
  const [customerListCount, setCustomerListCount] = useState(0);
  const [selectedColumn, setSelectedColumn] = useState(customerSelectedColumn);
  const gridRef = useRef();

  // filter Object
  const customerFilter = {
    pageIndex: 0,
    pageSize: 10,
    status: null,
    serialNo: null,
    asNo: null,
    customerName: null,
    customerCode: null,
    mobile: null,
    area: null,
    plan: null,
    group: null,
    stbNo: null,
    balance: null,
    billDate: null,
    lastUpdate: null,
    sortBy: null,
  };

  useEffect(() => {
    Axios({
      url: "getCustomerListByAdmin/" + admin.id,
      method: "post",
      data: customerFilter,
    }).then((res) => {
      setCustomerList((customerList) => [
        ...customerList,
        ...res.data.customerList,
      ]);
      setCustomerListCount(res.data.customerCount);
      const allColumnIds = [];
      gridRef.current.columnApi.getAllColumns().forEach((column) => {
        allColumnIds.push(column.getId());
      });
      gridRef.current.columnApi.autoSizeColumns(allColumnIds, false);
    });
  }, []);

  const defaultColDef = {
    sortable: true,
    unSortIcon: true,
    wrapText: true,
    resizable: true,
    autoHeight: true,
  };

  const statusFilter = () => {
    return <CustomColumnFilterDropdown options={["active", "Inactive"]} />;
  };

  const columnDefs = [
    {
      headerName: "Status",
      field: "status",
      cellRenderer: (data) => {
        return CustomActive(data.data.customerActive);
      },
      filter: "agTextColumnFilter",
      floatingFilter: true,
      editable: true,
      floatingFilterComponent: statusFilter,
      floatingFilterComponentParams: { suppressFilterButton: true },
    },
    {
      headerName: "S No",
      field: "serialNo",
      filter: "agTextColumnFilter",
      floatingFilter: true,
      editable: true,
      comparator: () => 0,
      floatingFilterComponent: CustomFilterInput,
      floatingFilterComponentParams: { suppressFilterButton: true },
    },
    { headerName: "AS No", field: "asNo" },
    { headerName: "Name", field: "customerName" },
    { headerName: "Code", field: "customerCode" },
    { headerName: "Customer Login", field: "customerLoginId" },
    { headerName: "Phone", field: "mobile" },
    { headerName: "Area", field: "areaId.aName" },
    { headerName: "Plan", field: "planId.pName" },
    { headerName: "Group", field: "groupId.gName" },
    { headerName: "STB No", field: "custStbNo" },
    { headerName: "Balance", field: "balance" },
    {
      headerName: "Bill Date",
      field: "expiryDate",
      valueFormatter: (data) => {
        return moment(data.data.expiryDate).format("DD-MM-YYYY");
      },
    },
    {
      headerName: "Last Updated on",
      field: "aDate",
      valueFormatter: (data) => {
        return moment(data.data.lastUpdate).format("DD-MM-YYYY");
      },
    },
    {
      headerName: "Actions",
      width: 125,
      cellRenderer: () => {
        // here, I was hoping the "data" would refer to the entire
        // row / object being bound, so that I could check for
        // certain conditions to be true (or false)
        return (
          <>
            <EditIcon sx={{ margin: "5px" }} />
            <ClearIcon sx={{ margin: "5px" }} />{" "}
          </>
        );
      },
    },
  ];

  const handleButtonClick = () => {
    console.log("handleButtonClick");
  };

  const onGridReady = () => {
    columnDefs.forEach((c) => {
      if (selectedColumn.some((sc) => sc?.value === c.field)) {
        gridRef.current.columnApi.setColumnVisible(c.field, true);
      } else {
        gridRef.current.columnApi.setColumnVisible(c.field, false);
      }
    });
  };

  const handleMultiSelect = (e) => {
    setSelectedColumn(e);
    dispatch(setCustomerSelectedColumn(e));
    columnDefs.forEach((c) => {
      if (e.some((sc) => sc?.value === c.field)) {
        gridRef.current.columnApi.setColumnVisible(c.field, true);
      } else {
        gridRef.current.columnApi.setColumnVisible(c.field, false);
      }
    });
  };

  const handlePageChange = (newPage) => {
    customerFilter.pageIndex = newPage;
    Axios({
      url: "getCustomerListByAdmin/" + admin.id,
      method: "post",
      data: customerFilter,
    }).then((res) => {
      setCustomerList([...res.data.customerList]);
    });
  };

  const handleChangeRowsPerPage = (pageRow) => {
    customerFilter.pageSize = pageRow !== -1 ? pageRow : customerListCount;
    Axios({
      url: "getCustomerListByAdmin/" + admin.id,
      method: "post",
      data: customerFilter,
    }).then((res) => {
      setCustomerList([...res.data.customerList]);
    });
  };

  const handleExcelExport = () => {
    gridRef.current.api.exportDataAsExcel({
      fileName: "Today",
      columnKeys: selectedColumn.map((c) => c?.value),
    });
  };

  return (
    <>
      <PageHeader
        header={"Customer Management"}
        btnText={"Add Customer"}
        handleButtonClick={handleButtonClick}
        handleExcelExport={handleExcelExport}
      ></PageHeader>
      {/* <CommonBtns
        btnText={"Add Customer"}
        handleButtonClick={handleButtonClick}
      ></CommonBtns> */}
      <CustomFilter>
        <div style={{ marginTop: "4px", marginLeft: "4px" }}>
          <Button
            sx={{ height: "32px", float: "left" }}
            variant="contained"
            endIcon={
              <FontAwesomeIcon style={{ height: "12px" }} icon={faUpload} />
            }
          >
            Import
          </Button>
          <Button
            sx={{
              height: "32px",
              marginLeft: "4px !important",
              backgroundColor: "red !important",
              color: "white !important",
              float: "left",
            }}
            variant="contained"
          >
            Clear Filter
          </Button>
          {/* <ColumnFilter columns={customerColumn} handleColumnFilter={handleColumnFilter} /> */}
          <$MultiSelect
            options={customerColumn}
            value={selectedColumn}
            onChange={(e) => handleMultiSelect(e)}
            labelledBy="Select"
          />
        </div>
      </CustomFilter>
      <CustomPagination
        totalRow={customerListCount}
        handlePageChange={handlePageChange}
        handleChangeRowsPerPage={handleChangeRowsPerPage}
      />
      <$GridHolder className="ag-theme-alpine" drawer={drawer}>
        <AgGridReact
          ref={gridRef}
          rowData={customerList}
          columnDefs={columnDefs}
          defaultColDef={defaultColDef}
          enableRangeSelection={true}
          onGridReady={onGridReady}
          onSortChanged={(e) =>
            console.log("e", gridRef.current.columnApi.getColumnState())
          }
          serverSideStoreType={"partial"}
          onFilterChanged={() =>
            console.log(gridRef.current.api.getFilterModel())
          }
          sideBar={true}
        ></AgGridReact>
      </$GridHolder>
    </>
  );
};

export default CustomerList;

const $GridHolder = styled.div`
  height: 26em;
  // max-width: ${(prop) => (prop.drawer ? `80%` : `98%`)};
  margin: 1em;
`;

const $MultiSelect = styled(MultiSelect)`
  width: 25%;
  top: -35px;
  float: left;
  margin: 0 10px;
`;
